import { Button } from '@mui/material';
import axios from 'axios';
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { HashLoader } from 'react-spinners';
import { DataContext } from '..';
import ResultTable from '../components/ResultTable';

const Result = () => {
  const dataArray = React.useContext(DataContext);
  const [loading, setLoading] = React.useState(true);

  const handleSubmit = async () => {
    setLoading(true);
    const domains = dataArray[0];
    const timeFrame = dataArray[2]?.timeFrame;
    const limit = dataArray[3]?.limit;
    const data = await axios.post(
      'https://lime-brainy-squirrel.cyclic.app/api',
      {
        domains,
        timeFrame,
        limit,
      }
    );

    dataArray[1] = data.data;
    setLoading(false);
  };

  useEffect(() => {
    if (dataArray[1] && dataArray[1].length > 0) {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      await handleSubmit();
    };
    if (!dataArray[1]) {
      fetchData();
    }
  }, []);

  const navigate = useNavigate();

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100vh',
      }}>
      <Button
        variant='contained'
        component='label'
        sx={{ margin: '20px' }}
        onClick={() => {
          navigate('/');
        }}>
        Back to home
      </Button>
      <br />
      {loading ? <HashLoader color='#36d7b7' size={200} /> : <ResultTable />}
    </div>
  );
};

export default Result;
